/* Global styles */

html,
body {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  overflow: hidden;
}

body {
  background-color: #ffffff;
}

.login-image {
  background-image: url(/etc.clientlibs/reactapp/clientlibs/clientlib-react/resources/static/media/login.96106f21.png);
  height: 100vh;
  max-height: 1080px;
  background-position: center;
}

.form {
  background: linear-gradient(180deg, #33383d 0%, #1c1d20 100%);
  height: 100vh;
  max-height: 1080px;
}

.home-background {
  background: linear-gradient(105.96deg, #FFFFFF 0%, #F0F0F0 97.86%);
  height: 100vh;
  max-height: 1080px;
}

